import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class PaymentMethodService {

  constructor(
    private http: HttpClient, 
  ) { }


  getAllPaymentMethods(): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/all`;
    return this.http.get<any[]>(paymentMethodUrl);
  }
  getPaymentMethodsByStatus(params): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/fetchbyStatus`;
    return this.http.get<any[]>(GLAccountManagementUrl, { params });
  }
  getPaymentMethodById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/PaymentMethod/find/by/id`, {params}
    );
  }
  addPaymentMethods(body: any): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/create`;
    return this.http.post<any[]>(paymentMethodUrl, body);
  }

  deletePaymentMethod(params: any): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/delete`;
    return this.http.delete<any[]>(paymentMethodUrl, { params });
  }
  

  updatePaymentMethod(dataBody: any): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/update`;
    return this.http.put<any[]>(paymentMethodUrl, dataBody);
  }

  // validatePaymentMethod(params: any): Observable<any> {
  //   const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/modifyStatus`;
  //   return this.http.put<any>(
  //     paymentMethodUrl,
  //     {},
  //     {
  //       params: params,
  //     })
  // }

  uploadBatchPaymentMethods(items: any,): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/create/bulk`;
    return this.http.post<any>(paymentMethodUrl, items)
  }



  validateBulkPaymentMethods(processedRows: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/PaymentMethod/update/state`;
    return this.http.put(
      expenseUrl,
      processedRows,
    );
  }


  //*************************************************************************************************
  //Customer Types */

  getAllCustomerTypes(): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/customertypes/all`;
    return this.http.get<any[]>(paymentMethodUrl);
  }


  getCustomerTypesByStatus(params): Observable<any> {
    const GLAccountManagementUrl = `${environment.baseUrlFinance}/api/v1/customertypes/fetchbyStatus`;
    return this.http.get<any[]>(GLAccountManagementUrl, { params });
  }

  getCustomerTypeById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/customertypes/find/by/id`, {params}
    );
  }

  addCustomerTypes(dataBody: any): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/customertypes/create`;
    return this.http.post<any[]>(paymentMethodUrl, dataBody);
  }

  deleteCustomerType(params: any): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/customertypes/delete`;
    return this.http.delete<any[]>(paymentMethodUrl, { params });
  }

  updateCustomerType(dataBody: any): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/customertypes/update`;
    return this.http.put<any[]>(paymentMethodUrl, dataBody);
  }



  uploadCustomerTypes(items: any,): Observable<any> {
    const paymentMethodUrl = `${environment.baseUrlFinance}/api/v1/customertypes/create/bulk`;
    return this.http.post<any>(paymentMethodUrl, items)
  }

  validateBulkCustomerTypes(processedRows: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/customertypes/update/state`;
    return this.http.put(
      expenseUrl,
      processedRows,
    );
  }


}
