

import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, map } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class ApplicationMatrixService {

  constructor(private http: HttpClient) { }

  getApplicationMatrix(): Observable<any> {
    const ApplicationMatrixUrl = `${environment.baseUrlProcurement}/api/v1/rankingmatrix/all`;
    return this.http.get<any[]>(ApplicationMatrixUrl);
  }
  getApplicationMatrixById(params: any): Observable<any> {
    const ApplicationMatrixssUrl = `${environment.baseUrlProcurement}/api/v1/rankingmatrix/find/by/id`;

    return this.http.get<any>(ApplicationMatrixssUrl, { params: params, });

  }

  addApplicationMatrix(data: any): Observable<any> {
    const ApplicationMatrixUrl = `${environment.baseUrlProcurement}/api/v1/rankingmatrix/add`;
    return this.http.post<any>(ApplicationMatrixUrl, data);
  }

  updateApplicationMatrix(ApplicationMatrixDetails: any): Observable<any> {
    const ApplicationMatrix = `${environment.baseUrlProcurement}/api/v1/rankingmatrix/modify`;
    return this.http.put<any>(ApplicationMatrix, ApplicationMatrixDetails)
  }


  deleteApplicationMatrixTemporarily(params: any): Observable<any> {
    const ApplicationMatrixUrl = `${environment.baseUrlProcurement}/api/v1/rankingmatrix/delete`;
    return this.http.delete<any>(ApplicationMatrixUrl, { params });
  }


  getApplicationMatrixByCode(params: any): Observable<any> {
    const ApplicationMatrixssUrl = `${environment.baseUrlProcurement}/api/v1/rankingmatrix/find/by/refcode`;
    return this.http.get<any>(ApplicationMatrixssUrl, { params: params, });
  }


}